//Add to cart
export const addItemToCart = (item,next)=>{ //next to get a callback to make use redirect to cart page
    let cart = [];
    if (typeof window !== undefined){
        if(localStorage.getItem("cart")){
            cart = JSON.parse(localStorage.getItem("cart"));
        }
        cart.push({
            ...item,
            count:1
        })
        localStorage.setItem("cart",JSON.stringify(cart));
        next();
    }
}

//Load in cart items
export const loadCart = ()=>{
    if (typeof window !== undefined){
        if(localStorage.getItem("cart")){
           return JSON.parse(localStorage.getItem("cart"));
        }
    }
};


//Remove cart item
export const removeItemFromCart = (productId)=>{
    let cart = [];
    if (typeof window !== undefined){
        if(localStorage.getItem("cart")){
            cart = JSON.parse(localStorage.getItem("cart"));
        }
        
        cart.map((product,index)=>{
            if(product._id === productId){
                cart.splice(index,1)
            }
        })
        localStorage.setItem("cart",JSON.stringify(cart));
    }
    return cart;
}

//Empty out the cart and force reload after payment
export const cartEmpty=(next)=>{
    if (typeof window !== undefined){
        localStorage.removeItem("cart");
        //below it is requird to keep the cart but empty it after transaction
        let cart = [];
        localStorage.setItem("cart",JSON.stringify(cart));
        next();
    }
}
