import React, { useState } from "react";
import Base from "../core/Base";
import { Link, Redirect } from "react-router-dom";

import { signin, authenticate, isAuthenticated } from "../auth/helper";

const Signin = () => {
  const [values, setValues] = useState({
    email: "theuniqueraj@gmail.com",
    password: "123123",
    error: "",
    loading: false, //to wait use till authenticated
    didRedirect: false, //redirect to user dash
  });

  const { email, password, error, loading, didRedirect } = values;

  //Store the token here
  const { user } = isAuthenticated();

  const handleChange = (key) => (event) => {
    setValues({
      ...values,
      error: "",
      [key]: event.target.value,
    });
  };

  const onSubmit = (e) => {
    e.preventDefault();
    setValues({
      ...values,
      error: false,
      loading: true,
    });
    signin({ email, password })
      .then((data) => {
        if (data.error) {
          setValues({
            ...values,
            error: data.error,
            loading: false,
          });
        } else {
          authenticate(data, () => {
            setValues({
              ...values,
              email: "",
              password: "",
              error: "",
              didRedirect: true,
            });
          }); //if we use next() we can fire a callback
        }
      })
      .catch((err) => {
        console.log("Signin Failed/Db not accessible");
      });
  };

  const performRedirect = () => {
    if (didRedirect) {
      if (user && user.role == 1) {
        //user comes from is authenticated
        return <Redirect to="/admin/dashboard/" />;
      } else {
        return <Redirect to="/user/dashboard/" />;
      }
    }

    console.log(isAuthenticated());
    if (isAuthenticated()) {
      return <Redirect to="/" />;
    }
  };

  const loadingMessage = () => {
    return (
      loading && (
        <div
          className="alert alert-info"
          style={{ display: loading ? "" : "none" }}
        >
          <h2> Loading...</h2>
        </div>
      )
    );
  };

  const errorMessage = () => {
    return (
      <div
        className="alert alert-danger"
        style={{ display: error ? "" : "none" }}
      >
        {error}
      </div>
    );
  };

  const signInForm = () => {
    return (
      <div className="row">
        <div className="col-md-6 offset-sm-3 text-left">
          {loadingMessage()}
          {errorMessage()}
          <form action="">
            <div className="form-group">
              <label className="text-light">Email</label>
              <input
                className="form-control"
                type="email"
                name=""
                id=""
                value={email}
                onChange={handleChange("email")}
              />
            </div>
            <div className="form-group">
              <label className="text-light">Password</label>
              <input
                className="form-control"
                type="password"
                name=""
                id=""
                value={password}
                onChange={handleChange("password")}
              />
            </div>
            <button className="btn btn-success btn-block" onClick={onSubmit}>
              Submit
            </button>
          </form>
        </div>
      </div>
    );
  };

  return (
    <Base>
      {signInForm()}
      {performRedirect()}
      <p className="text-white text-center">{JSON.stringify(values)}</p>
    </Base>
  );
};

export default Signin;
