import { API } from "../../backend";

export const getAllBlogs = ()=>{
    return fetch(`${API}/blog`,{
        method:"GET",
    }).then(response=>{
        return response.json();
    }).catch(err => console.log(err));
}

export const getSingleBlog = (blogId)=>{
    return fetch(`${API}/blog/${blogId}`,{
        method:"GET",
    }).then(response=>{
        return response.json();
    }).catch(err => console.log(err));
}