import React, {useState,useEffect} from 'react'
import ImageHelper from './helper/ImageHelper';
import { Redirect } from 'react-router-dom';
import { addItemToCart, removeItemFromCart } from './helper/CartHelper';

const Card=({
    product,
    addToCart = true,
    removeFromCart = false,
    setReload = f=>f, //function(f){return f} this is forcefull component to reload the page
    reload=undefined
}) =>{

    const [redirect, setRedirect] = useState(false);
    const [count, setCount] = useState(product.count)

    const cardTitle = product ? product.name : "This is another product name";
    const cardDescp = product ? product.description : "This is another product description";
    const cardPrice = product ? product.price : "This is another product price";

    const moveToCart= ()=>{
        addItemToCart(product,()=>{
            //from next in helper
            setRedirect(true);
        })
    }


    const getRedirect = (redirect)=>{
        if(redirect){
            return <Redirect to="/cart" />
        }
    }

    const showAddToCart = () =>{
        //FIXME: might cause prob
        return (
            addToCart && (
                <div className="col-12">
                    <button
                    onClick={moveToCart}
                    className="btn btn-block btn-outline-success mt-2 mb-2"
                    >
                    Add to Cart
                    </button>
                </div>
            )
        )
    }

    const showRemoveFromCart = () =>{
        //
        return (
            removeFromCart && (
                <div className="col-12">
                    <button
                    onClick={()=>{
                        removeItemFromCart(product._id);
                        console.log("Reload::",reload)
                        setReload(!reload);
                    }}
                    className="btn btn-block btn-outline-danger mt-2 mb-2"
                    >
                    Remove from cart
                    </button>
                </div>
            )
        )
    }


    return (
        <div className="card text-white bg-dark border border-info ">
          <div className="card-header lead">{cardTitle}</div>
          <div className="card-body">
              {getRedirect(redirect)}
            <div className="rounded border border-success p-2">
              <ImageHelper product={product} />
            </div>
            <p className="lead bg-success font-weight-normal text-wrap">
            {cardDescp}
            </p>
            <p className="btn btn-success rounded  btn-sm px-4">${cardPrice}</p>
            <div className="row">
              {showAddToCart()}
              {showRemoveFromCart()}
            </div>
          </div>
        </div>
      );
}
export default Card;