import React, {useState} from 'react'
import Base from '../core/Base';
import { isAuthenticated } from '../auth/helper';
import { Link } from 'react-router-dom';
import { createCategory } from './helper/adminapicall';



const AddCategory= ()=> {

    const [name, setName] = useState('');
    const [error, setError] = useState(false);
    const [success, setSuccess] = useState(false)

    const {user,token} = isAuthenticated();

    const goBack= ()=>{
        return (
            <div className="mt-5">
                <Link className="btn btn-sm btn-info mb-3" to='/admin/dashboard'>Admin Home</Link>
            </div>
        )
    }

    const handleChange = (event)=>{
        setError('');
        setName(event.target.value);
    }

    const onSubmit = (e)=>{
        e.preventDefault();
        setError('');
        setSuccess(false);

        //Calling the backend ... we need to pass name as {name} as in admin api helper we are using JSON.stringify
        createCategory(user._id,token,{name})
        .then(data=>{
            if(data.error){
                setError(true);
            } else{
                setError("");
                setSuccess(true);
                setName('');
            }
        })
    }

    const successMessage = ()=>{
        if(success){
            return <h4 className="text-success">Category Created Successfully</h4>
        }
    }
    const errorMessage = ()=>{
        if(error){
            return <h4 className="text-danger">Category Creation Failed</h4>
        }
    }

    const myCatForm = ()=>{
        return (
            <form action="">
                <div className="form-group">
                    {successMessage()}
                    {errorMessage()}
                    <p className="lead">Enter the Category</p>
                    <input type="text" className='form-control my-3' onChange={handleChange} value={name} autoFocus required placeholder='Summer' />
                    <button className="btn btn-outline-info rounded" onClick={onSubmit}>Create Category</button>
                </div>
            </form>
        )
    };



    return (
        <Base title='Create a Category here' description='add many as you want' className="container bg-info p-4">
            <div className="row bg-white rounded">
                <div className="col-md-8 offset-md-2">
                    {myCatForm()}
                    {goBack()}
                </div>
            </div>
        </Base>
    )
}

export default AddCategory;