import React, { useState, useEffect } from "react";
import "../styles.css";
import { API } from "../backend";
import Base from "./Base";
import Card from "./Card";
import { getAllBlogs } from "./helper/blogHelper";
import BlogCard from "./BlogCard";
import { Link } from "react-router-dom";

const Blog = () => {
  const [blogs, setBlogs] = useState([]);
  const [errors, setErrors] = useState(false);

  const loadAllBlogs = () => {
    getAllBlogs().then((data) => {
      if (data.error) {
        setErrors(data.error);
      } else {
        console.log(data);
        setBlogs(data);
      }
    });
  };

  useEffect(() => {
    loadAllBlogs();
  }, []);

  return (
    <Base className="container p-4">
      <div
        className="row py-5 justify-content-center-align-item-center text-center"
        style={{
          height: "60vh",
          backgroundColor: "#e74a3b",
          backgroundImage:
            "linear-gradient(180deg, #e74a3b 10%, #be2617 100%);",
          backgroundSize: "cover",
        }}
      >
        <div className="col align-self-center p-3">
          <p className="display-4 font-weight-bold text-white mb-5">
            <i class="fas fa-pen-nib"></i> Blogs
          </p>
          <p className="h2 font-weight-bold" style={{ color: "#00000055" }}>
            <i class="fas fa-quote-left fa-2x"></i>
            <span className="px-5">
              Beauty Begins The Moment You Decide To Be Yourself
            </span>
            <i class="fas fa-quote-right fa-2x"></i>
          </p>
        </div>
      </div>
      <div className=" text-center bg-light text-dark p-4">
        <div className="row mx-0 py-4">
          {blogs.map((blogs, index) => {
            return (
              // <Link className="nav-link" to={`/blog/${blogs._id}`}>
              <BlogCard blogs={blogs} key={index} />
              // </Link>
            );
          })}
        </div>
      </div>
    </Base>
  );
};
export default Blog;
