import React, { useState, useEffect } from 'react';
import '../styles.css';
import {API} from '../backend';
import Base from './Base';
import Card from './Card';
import { loadCart } from './helper/CartHelper';
import PaymentB from './PaymentB';

const Cart= ()=> {
    
    const [products, setProducts] = useState([]);
    const [reload, setReload] = useState(false) //forcefull reload for remove in cart this func does nothing only changes reload from true to false and false to true....07
    
    useEffect(() => {
        setProducts(loadCart());
    },[reload]); //whenever there is a change to this reload or forcefull component react will remount/reload everything
    

    const loadAllProducts = (products)=>{
        //load all products from localStorage of browser
        return (
            <div>
                <h1>For Loading</h1>
                {products.map((product,index)=>(
                    <Card key={index} 
                        product={product}
                        addToCart={false}
                        removeFromCart={true}
                        setReload={setReload}
                        reload={reload}
                    />
                ))}
            </div>
        )
    }
    
   /* 
   //If more than one payment operator needed use this
    const loadCheckout = ()=>{
        //load all products from localStorage of browser
        return (
            <h1>For Checkout</h1>
        )
    }
     */

    return (
        <Base title="Cart Page" description="Welcome to your Cart" style={{"height":"100%"}} className="container p-4">
            <div className="row text-center bg-light text-dark p-4">
                <div className="col-6">
                    {products.length > 0 ? loadAllProducts(products) : (<h3>No Products in Cart</h3>)}
                </div>
                <div className="col-6">
                    <PaymentB products={products} setReload={setReload} />
                </div>
            </div>
        </Base>
    )
}

export default Cart;