import React, { useState, useEffect } from "react";
import { API } from "../backend";
import { getSingleBlog } from "./helper/blogHelper";

import Base from "./Base";
import { Link } from "react-router-dom";

const EachBlog = ({ match }) => {
  const { blogId } = match.params;
  console.log(blogId);
  const [blog, setBlog] = useState();
  const [loading, setLoading] = useState(true);

  const preload = (blogId) => {
    getSingleBlog(blogId).then((data) => {
      if (data) {
        setBlog(data);
        setLoading(false);
      }
    });
  };

  useEffect(() => {
    preload(blogId);
  }, []);

  if (loading) {
    return (
      <div>
        <h1>Loading</h1>
      </div>
    );
  }

  return (
    <Base className="p-4">
      {console.log(blog)}
      <section
        className="blog-intro p-5"
        style={{
          backgroundColor: "#e74a3b",
          backgroundImage:
            "linear-gradient(180deg, #e74a3b 10%, #be2617 100%);",
          backgroundSize: "cover",
        }}
      >
        <div className="container-fluid p-0 h-100">
          <div className="row py-5 justify-content-center-align-item-center text-center">
            <div className="col align-self-center p-3">
              <p className="display-4 font-weight-bold text-white mb-5">
                <Link to="/blogs" class="text-white">
                  Blogs
                </Link>{" "}
                / {blog.title}
              </p>
              <p className="h2 font-weight-bold" style={{ color: "#00000055" }}>
                <i class="fas fa-quote-left fa-2x"></i>
                <span className="px-5">
                  Happiness Is A Habit, So Is Your Skincare
                </span>
                <i class="fas fa-quote-right fa-2x"></i>
              </p>
            </div>
          </div>
        </div>
        <div
          className="container h-100 border bg-light shadow-lg"
          style={{
            borderRadius: "10px",
          }}
        >
          <div className="row justify-content-center align-items-center w-100 p-5 border-left-warning">
            <div className="col-lg-8 col-sm-12 align-self-center">
              <img
                src={`${API}/blog/largePhoto/${blogId}`}
                alt=""
                className="img-fluid"
              />
              <p className="h2 font-weight-bold pt-4">{blog.title}</p>
              <p className="post-author text-muted blockquote-footer">
                {" "}
                {blog.authorName}
                &nbsp;&nbsp;&nbsp;&nbsp;
                {new Date(blog.createdAt).toDateString().replace(" ", ",")}
              </p>
              <hr />
              <p className="text-muted">{blog.description}</p>
              <hr className="mt-5" />
            </div>
            <div className="col-lg-4 col-sm-12 align-self-start">
              <p className="h4 font-weight-bold">Latest Posts</p>
              <div className="latest-posts">
                <div className="row mx-auto my-0">
                  <div className="col-3 align-self-center p-0">
                    <img
                      src={`${API}/blog/smallPhoto/${blogId}`}
                      alt=""
                      className="post-side-img img-fluid rounded-circle"
                    />
                  </div>
                  <div className="col-9 align-self-center text-left">
                    <div className="side-post-details">
                      <h5 className="font-weight-bold m-0">
                        New Ideas Of Post
                      </h5>
                      <p className="side-pen text-muted small lead p-0 m-0">
                        Author - Debraj Sengupta
                      </p>
                    </div>
                  </div>
                </div>
                <hr />
                <div className="row mx-auto my-0">
                  <div className="col-3 align-self-center p-0">
                    <img
                      src={`${API}/blog/smallPhoto/${blogId}`}
                      alt=""
                      className="post-side-img img-fluid rounded-circle"
                    />
                  </div>
                  <div className="col-9 align-self-center text-left">
                    <div className="side-post-details">
                      <h5 className="font-weight-bold m-0">
                        New Ideas Of Post
                      </h5>
                      <p className="side-pen text-muted lead small p-0 m-0">
                        Author - Debraj Sengupta
                      </p>
                    </div>
                  </div>
                </div>
                <hr />
                <div className="row mx-auto my-0">
                  <div className="col-3 align-self-center p-0">
                    <img
                      src={`${API}/blog/smallPhoto/${blogId}`}
                      alt=""
                      className="post-side-img img-fluid rounded-circle"
                    />
                  </div>
                  <div className="col-9 align-self-center text-left">
                    <div className="side-post-details">
                      <h5 className="font-weight-bold m-0">
                        New Ideas Of Post
                      </h5>
                      <p className="side-pen text-muted small lead p-0 m-0">
                        Author - Debraj Sengupta
                      </p>
                    </div>
                  </div>
                </div>
                <hr />
                <div className="row mx-auto my-0">
                  <div className="col-3 align-self-center p-0">
                    <img
                      src={`${API}/blog/smallPhoto/${blogId}`}
                      alt=""
                      className="post-side-img img-fluid rounded-circle"
                    />
                  </div>
                  <div className="col-9 align-self-center text-left">
                    <div className="side-post-details">
                      <h5 className="font-weight-bold m-0">
                        New Ideas Of Post
                      </h5>
                      <p className="side-pen text-muted small lead p-0 m-0">
                        Author - Debraj Sengupta
                      </p>
                    </div>
                  </div>
                </div>
              </div>
              <div className="about-author text-center mt-5">
                <img
                  src={`${API}/blog/authorPhoto/${blogId}`}
                  alt=""
                  className="author-thumb img-fluid img-thumbnail rounded-circle mt-4"
                />
                <p className="lead font-weight-bold m-0">{blog.authorName}</p>
                <p className="text-muted m-0">{blog.authorTag}</p>
                <div className="social-icons">
                  <i className="fab fa-facebook-f">&nbsp;&nbsp;</i>
                  <i className="fa fa-twitter"></i>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Base>
  );
};

export default EachBlog;
