import React, {useState, useEffect} from 'react';
import { loadCart, cartEmpty } from './helper/CartHelper';
import { Link } from 'react-router-dom';
import { getToken, processPayment } from './helper/paymentBHelper';
import { createOrder } from './helper/OrderHelper';
import { isAuthenticated } from '../auth/helper';
import DropIn from "braintree-web-drop-in-react";


const PaymentB = ({products,setReload = f=>f , reload = undefined})=> {

    console.log(products);

    const [info, setInfo] = useState({
        loading:false,
        success:false,
        clientToken: null,
        instance:{},
        error:""
    });

    
    const userId = isAuthenticated() && isAuthenticated().user._id;
    const token = isAuthenticated() && isAuthenticated().token;
    
    const getBT_Token = (userId,token)=>{
        getToken(userId,token).then(info=>{
            console.log("Information::",info);
            if(info.error){
                setInfo({
                ...info,
                error:info.error
                });
            }else{
                const clientToken = info.clientToken;
                setInfo({clientToken});
            }
        })
    }


    const showBT_DropInUi = ()=>{
        return (
            <div>
                {(isAuthenticated() && info.clientToken !== null && products.length) > 0 ? (
                     <div>
                        <DropIn
                        options={{ authorization: info.clientToken }}
                        onInstance={(instance) => (info.instance = instance)}
                        />
                        <button className="btn btn-success btn-block" onClick={onPurchase}>Buy</button>
                    </div>
                ) : (
                    <h3>Please Login Or Add Something to Cart</h3>
                ) }
            </div>
        )
    }

    useEffect(() => {
        getBT_Token(userId,token);
    }, []);

    const onPurchase = () =>{
        setInfo({loading: true});
        let nonce;
        let getNonce = info.instance
        .requestPaymentMethod()
        .then(data=>{
            //I want to make an payment
            nonce = data.nonce;
            const paymentData = {
                paymentMethodNonce:nonce,
                amount:getAmount(),
            };
            //I want to make settlement
            processPayment(userId,token,paymentData)
            .then(response=>{
                setInfo({
                    ...info,
                    success: response.success,
                    loading:false
                })
                console.log("PS");

                //Create the order at db
                const orderData = {
                    products: products,
                    transaction_id: response.transaction_id,
                    amount: response.transaction.amount
                }
                createOrder(userId,token,orderData);

                cartEmpty(()=>{ //emptying the cart after purchase
                    console.log("Did we got a crash");
                });
                setReload(!reload);
            })
            .catch(err=>{
                setInfo({
                    ...info,
                    loading:false,
                    success:false
                })
                console.log("CF")
            });
        })
        .catch(err=>console.log(err));
    }

    const getAmount = ()=>{
        let amount = 0;
        products.map((product,index)=>{
            amount+=product.price;
        })
        return amount;
    }


    return (
        <div>
            <h3>Your Bill is {getAmount()}</h3>
            <h3>{showBT_DropInUi()}</h3>        
        </div>
    )
}

export default PaymentB;