import React, { useState, useEffect } from "react";
import { API } from "../backend";
import Base from "./Base";
import Card from "./Card";
import { getAllProducts } from "./helper/coreapicalls";
import cg from "../img/cg.png";
import "../coming_soon_css.css";

export default function Home() {
  const [products, setProducts] = useState([]);
  const [errors, setErrors] = useState(false);

  const loadAllProducts = () => {
    getAllProducts().then((data) => {
      if (data.error) {
        setErrors(data.error);
      } else {
        setProducts(data);
      }
    });
  };

  useEffect(() => {
    loadAllProducts();
  }, []);

  return (
    <Base classNameName="container p-4">
      <section className="container-fluid d-flex h-100 p-0">
        <div className="row cont w-100 justify-content-center align-items-center m-0 p-0">
          <div className="col-md-6 align-self-start">
            <div className="text-left">
              <img
                src={cg}
                alt="Circle Skincare"
                style={{ width: "500px", maxWidth: "100%" }}
              />
            </div>
          </div>
          <div className="col-md-6 col-6-faded align-self-center justify-content-center">
            <p className="text-muted font-weight-bold h1 ml-md-5 p-5 ">
              We are customizing best products for you.
              <br />
              <strong style={{ color: "#222326bb" }}>
                Stay tuned to get Just What Your Skin Needs.
              </strong>
            </p>
          </div>
        </div>
      </section>
    </Base>
  );
}
