import React from 'react'
import { API } from '../../backend';


const ImageHelper = ({product})=> {
    const imgUrl = product ? `${API}/product/photo/${product._id}` : "https://images.pexels.com/photos/4110226/pexels-photo-4110226.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500";
    return (
            <img
                src={imgUrl}
                alt="photo"
                style={{ maxHeight: "100%", maxWidth: "100%" }}
                className="mb-3 rounded"
              />
    )
}

export default ImageHelper;